/* =====================================================================
 * Media Query == XS == extra small screen | phone
 * =====================================================================
 */

@media screen and (max-width: @screen-xs-max) {

  /* ==========================================
 * Fancybox Lightbox
 * ========================================== */

  .fancybox-wrap {
    .fancybox-skin{
      padding: 15px 45px 15px 15px;
    }

    &.info-layer{
      .info-dialog{
        width: 660px;
      }

      .info-content{
        padding: 20px 15px 20px 30px;
        overflow: hidden;
      }

      .info-header,
      .info-body,
      .info-footer{
        border: none;
        padding: 0;
        margin: 0;
      }

      .info-title{
        font-size: 20px;
        font-size: 2rem;
        margin-bottom: 5px;
      }

      .info-body{
        margin-bottom: 25px;
        font-size: 16px;
        font-size: 1.6rem;
      }

      .info-footer{
        text-align: left;
        a{
          font-size: 14px;
          font-size: 1.4rem;
          &:after{
            top: 3px;
            right: 0;
          }
        }
        .btn{ padding: 10px 34px; }
      }

      #privacyPolicy{
        .info-dialog{
          width: auto;
          .info-content{ padding: 20px 30px 30px 30px; }
          .info-header{
            margin-bottom: 15px;
            padding-right: 30px;
            line-height: 1.3em;
          }
          .info-body{
            font-size: 14px;
            font-size: 1.4rem;
            margin-bottom: 0;
            .company-info{
              width: auto;
              margin-right: 0;
              margin-bottom: 10px;
              .headline{
                font-size: 20px;
                font-size: 2rem;
              }
            }
            .legal-info{
              width: auto;
              margin-right: 0;
              margin-bottom: 10px;
            }
            .links{
              width: auto;
            }

            ul {
              li {
                font-size: 14px;
                font-size: 1.4rem;
                &:before {
                  top: 2px;
                  left: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}