/* =====================================================================
 * Media Query == SM == small screen | tablet
 * =====================================================================
 */

@media screen and (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {

  /* ==========================================
 * Fancybox Lightbox
 * ========================================== */

  .fancybox-wrap {
    &.info-layer{
      .info-dialog{
        width: 660px;
      }

      .info-content{
        padding: 20px 15px 20px 30px;
        overflow: hidden;
      }

      .info-header,
      .info-body,
      .info-footer{
        border: none;
        padding: 0;
        margin: 0;
      }

      .info-title{
        font-size: 20px;
        font-size: 2rem;
        margin-bottom: 5px;
      }

      .info-body{
        margin-bottom: 25px;
        font-size: 16px;
        font-size: 1.6rem;
      }

      .info-footer{
        text-align: left;
        a{
          font-size: 14px;
          font-size: 1.4rem;
          &:after{
            top: 3px;
            right: 0;
          }
        }
        .btn{ padding: 10px 34px; }
      }

      #privacyPolicy{
        .info-dialog{
          width: 660px;

          .info-header{
            margin-bottom: 30px;
          }
          .info-body{
            font-size: 14px;
            font-size: 1.4rem;
            margin-bottom: 0;
            .company-info{
              width: 190px;
              margin-right: 10px;
              .headline{
                font-size: 20px;
                font-size: 2rem;
              }
            }
            .legal-info{
              width: 245px;
              margin-right: 20px;
            }
            .links{
              width: 150px;
            }

            ul {
              li {
                font-size: 14px;
                font-size: 1.4rem;
                &:before {
                  top: 2px;
                  left: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}