@import (once) "daimler-fonts";
@import (once) "daimler-icons";
@import (once) "daimler-colors";

/*
 * Header
 *
 */
header {
  background: #000 url("../img/img_header_brushed_bg_lg.jpg") repeat center top;
  border-bottom: 0 none;
  margin-bottom: 0;
  min-height: 115px;

  .page-header {
    border: medium none;
    margin-bottom: 0;
    margin-top: 0;
    padding-bottom: 0;
    padding-top: 28px;
    position: relative;
  }

  .page-header .page-header-nav ul {
    color: #000;
    font-size: 1.3rem;
    height: 50px;
    letter-spacing: normal;
    list-style: outside none none;
    margin: 0;
    padding: 0;
    position: absolute;
    right: 10px;
    top: -24px;
  }

  .page-header .page-header-nav ul li {
    display: inline;
    list-style: outside none none;
    margin: 0;
    padding: 0;
  }

  .page-header .page-header-nav ul li:first-child::before {
    display: none;
  }

  .page-header .page-header-nav ul li.meta-standard {
    padding-right: 4px;
  }

  .page-header .page-header-nav ul li a {
    color: #707070;
  }

  .page-header .page-header-nav ul li a:hover {
    color: #000;
  }

  .page-header .logo-wrapper {
    margin-top: 8px;
  }

  .page-header .logo-wrapper img {
    height: 26px;
  }

  .page-header .buttons-wrapper {
    bottom: -12px;
    height: 35px;
    left: 13px;
    position: absolute;
    width: 33%;
  }

  .page-header .page-title-wrapper {
    font-size: 2.6rem;
    line-height: 1.2;
    margin-top: 9px;
    text-align: right;
  }

  .page-header .page-title-wrapper span {
    display: block;
    font-family: "DaimlerCS-Regular-Italic", Arial, sans-serif;
    font-size: 1.2rem;
    line-height: 1;
  }
}

.privacy-disclaimer {
  color: #000;
  font-size: 1.3rem;
  letter-spacing: normal;
  padding: 0 20px 0 0;

  &:after{
    background: transparent url("../img/img_assets_sprite_svg.svg") no-repeat scroll -23px -38px;
    content: "";
    display: inline-block;
    height: 13px;
    line-height: 13px;
    position: absolute;
    right: 0;
    top: 2px;
    width: 14px;
  }

  &:hover{
    color: #707070;

    &:after{
      background-position: -23px -16px;
    }
  }
}

/*
 * Main navigation
 *
 * Turn the `.navbar` at the top of the docs purple.
 */

.bs-docs-nav {
  margin-bottom: 0;
  background: #000 url("../img/img_header_brushed_bg_lg.jpg") repeat center top;
  border-bottom: 0;
  min-height: 115px;

  .navbar-header {
    width: 186px;
    height: 26px;
    background: transparent url("../img/img_logo_daimler_svg.svg") no-repeat top left;
    background-size: contain;
    text-indent: 100%;
    overflow: hidden;
    white-space: nowrap;
    position: relative;
    margin-top: 33px;
    float: none;
  }

  .navbar-right {
    display: none;
  }

  .bs-home-nav .bs-nav-b {
    display: none;
  }

  .navbar-brand,
  .navbar-nav > li > a {
    font-weight: 500;
    color: #000;
    padding-right: 0;
    padding-left: 10px;
    padding-top: 0;
  }

  .navbar-nav > li > a:before {
    content: '/';
    position: relative;
    margin-right: 10px;
  }

  .navbar-nav > li:first-child > a {
    padding-left: 0;
  }

  .navbar-nav > li:first-child > a:before {
    display: none;
  }

  .navbar-nav > li > a:hover,
  .navbar-nav > .active > a,
  .navbar-nav > .active > a:hover {
    color: #fff;
    background-color: transparent;
  }

  .navbar-toggle .icon-bar {
    background-color: #004355;
  }

  .navbar-header .navbar-toggle {
    border-color: #fff;
  }

  .navbar-header .navbar-toggle:hover,
  .navbar-header .navbar-toggle:focus {
    background-color: #f9f9f9;
    border-color: #f9f9f9;
  }
}

#bs-navbar {
  position: relative;
  top: 0px;
}


/* ==========================================
 * Fancybox Lightbox
 * ========================================== */

.fancybox-wrap {
  overflow: hidden !important;

  .fancybox-skin{
    border-radius: 0;
    padding: 15px 45px 15px 15px;
  }

  &.info-layer{
    .info-dialog{
      width: 720px;
    }

    .info-content{
      padding: 40px 50px;
      overflow: hidden;
    }

    .info-header,
    .info-body,
    .info-footer{
      border: none;
      padding: 0;
      margin: 0;
    }

    .info-title{
      font-size: 25px;
      font-size: 2.5rem;
      font-family: @corporate-s-demi-regular;
      letter-spacing: -0.04em;
      margin-bottom: 5px;
    }

    .info-body{
      margin-bottom: 25px;
      font-size: 18px;
      font-size: 1.8rem;
    }

    .info-footer{
      text-align: left;
      overflow: hidden;
      a{
        font-size: 16px;
        font-size: 1.6rem;
        font-family: @corporate-s-demi-regular;
        position: relative;
        padding-right: 15px;
        &:after {
          content: '';
          display: inline-block;
          height: 14px;
          line-height: 14px;
          width: 10px;
          background: url("../img/img_assets_sprite_svg.svg") no-repeat 0 0;
          position: relative;
          top: 2px;
          right: 0;
          margin-left: 5px;
        }
        &:hover:after{
          background-position: -18px 0;
        }
      }
      .btn{ padding: 10px 34px; }
    }

    #privacyPolicy{
      z-index: 10000;
      .info-dialog{
        width: 960px;

        .info-header{
          margin-bottom: 30px;
        }
        .info-body{
          font-size: 16px;
          font-size: 1.6rem;
          line-height: 1.2em;
          margin-bottom: 0;
          overflow: hidden;
          .company-info{
            float: left;
            width: 220px;
            margin-right: 20px;
            .headline{
              font-size: 25px;
              font-size: 2.5rem;
              line-height: normal;
            }
          }
          .legal-info{
            float: left;
            width: 370px;
            margin-right: 20px;
          }
          .links{
            float: left;
            width: 230px;
          }

          ul {
            overflow: hidden;
            list-style: none;
            margin: 0;
            padding: 0;

            li {
              list-style: none;
              line-height: 1.2em;
              padding-left: 14px;
              position: relative;
              margin: 0 0 10px 0;
              font-size: 16px;
              font-size: 1.6rem;

              a {
                text-decoration: none;
                &:hover {
                  &:before {
                    background-position: -18px 0;
                  }
                }
                &:before {
                  content: '';
                  display: inline-block;
                  height: 14px;
                  line-height: 14px;
                  width: 10px;
                  background: url("../img/img_assets_sprite_svg.svg") no-repeat 0 0;
                  position: absolute;
                  top: 2px;
                  left: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}

/*
 * Footer
 *
 * Separated section of content at the bottom of all pages, save the homepage.
 */
footer {
  background: #fff none repeat scroll 0 0;
  box-shadow: 0 -5px 25px 0 rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 1000;
  margin-top: 50px;

  h3 {
    font-size: 2.5rem;
  }
  .footer-content-wrapper {
    background: #f0f1f2 none repeat scroll 0 0;
    padding: 25px 0 45px;
  }
  .footer-disclaimer {
    font-size: 1.3rem;
    line-height: 1.2;
    margin-top: 30px;
    padding: 0 0 10px;
  }
  .footer-disclaimer p {
    margin: 0 0 20px;
    padding: 0;
  }
  .footer-disclaimer a:hover, .footer-disclaimer a:active {
    text-decoration: underline;
  }
  .footer-quicklinks {
    border-bottom: 1px solid #c8c8c8;
    border-top: 1px solid #c8c8c8;
    margin-bottom: 25px;
    margin-top: 30px;
    padding: 9px 0 3px;
  }
  .footer-quicklinks .quicklinks-wrapper {
    text-align: center;
  }
  .footer-quicklinks .quicklinks-wrapper ul {
    display: inline-block;
    margin-left: 20px;
  }
  .footer-quicklinks .quicklinks-wrapper ul:first-child {
    display: inline-block;
    margin-left: 0;
    margin-right: 20px;
  }
  .footer-quicklinks .quicklinks-wrapper ul, .footer-quicklinks .quicklinks-wrapper ul li {
    color: #000;
    letter-spacing: normal;
    list-style: outside none none;
    margin: 0;
    padding: 0;
  }
  .footer-quicklinks .quicklinks-wrapper ul li {
    float: left;
    margin-left: 25px;
  }
  .footer-quicklinks .quicklinks-wrapper ul li:first-child {
    margin-left: 0;
  }
  .footer-quicklinks .quicklinks-wrapper ul li a {
    color: #000;
    display: block;
    padding: 10px;
  }
  .footer-quicklinks .quicklinks-wrapper ul li a:hover {
    color: #000;
  }
  .footer-quicklinks .quicklinks-wrapper ul li span {
    display: block;
    padding: 10px;
  }
  .footer-quicklinks .quicklinks-wrapper ul li .lng-switch span {
    padding: 0;
  }
  .footer-quicklinks .quicklinks-wrapper ul li.inactive, .footer-quicklinks .quicklinks-wrapper ul li .inactive, .footer-quicklinks .quicklinks-wrapper ul li a.inactive {
    color: #c8c8c8;
  }
  .footer-quicklinks .quicklinks-wrapper ul li.highlight, .footer-quicklinks .quicklinks-wrapper ul li .highlight, .footer-quicklinks .quicklinks-wrapper ul li a.highlight {
    /*font-family: "DaimlerCS-Demi",Arial,sans-serif;*/
  }
  .footer-brands {
    margin-bottom: 17px;
  }
  .footer-brands .brands-wrapper {
    text-align: center;
  }
  .footer-brands .brands-wrapper ul {
    display: inline-block;
    margin-left: 20px;
  }
  .footer-brands .brands-wrapper ul:first-child {
    display: inline-block;
    margin-left: 0;
  }
  .footer-brands .brands-wrapper ul, .footer-brands .brands-wrapper ul li {
    color: #444444;
    letter-spacing: normal;
    list-style: outside none none;
    margin: 0;
    padding: 0;
  }
  .footer-brands .brands-wrapper ul li {
    float: left;
    height: 70px;
    line-height: 70px;
    margin-left: 40px;
  }
  .footer-brands .brands-wrapper ul li:first-child {
    margin-left: 0;
  }
  .footer-brands .brands-wrapper ul li a {
    color: #444444;
    display: block;
    padding: 0;
    vertical-align: middle;
  }
  .footer-brands .brands-wrapper ul li a:hover {
    opacity: 0.5;
  }
  .footer-copy {
    color: #444444;
    font-size: 18px;
  }
}

